$background: #d0f0ff !default;
$color: #191919 !default;
$background-hover: #0099ff !default;
$color-hover: #fff !default;
$nav-color: $color !default;
$nav-color-hover: $color-hover !default;
$not-in-month: #000 !default;
$not-in-month-hover: #999 !default;
$disabled: #333 !default;
$day-of-week: $not-in-month-hover !default;

$selected: #000 !default;
$selected-background: #fff !default;

$today-color: #fff !default;
$today-background: #0099ff !default;

.pickmeup {
  position: absolute;
  width: 620px;
  right: 100% !important;
  background-color: $background;
  font-family: $secondary-font;
  font-size: 12px;
  touch-action: manipulation;
  * {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  &.pmu-flat {
    position: relative;
  }
  &.pmu-hidden {
    display: none;
  }
  .pmu-instance {
    display: inline-block;
    width: 310px;
    padding: 20px;
    vertical-align: top;
    &:not(:last-child) {
      border-right: 1px solid #fff;
    }
    .pmu-button {
      color: $color;
      cursor: pointer;
      outline: none;
      text-decoration: none;
    }
    .pmu-button:hover {
      background: $background-hover;
      color: $color-hover;
    }
    .pmu-not-in-month {
      display: none !important;
    }
    .pmu-disabled,
    .pmu-disabled:hover {
      color: $disabled;
      cursor: default;
    }
    .pmu-selected {
      background: $selected-background;
      color: $selected;
    }
    .pmu-today {
      background: $today-background;
      color: $today-color;
    }

    nav {
      display: flex;
      justify-content: space-between;
      margin-bottom: 18px;
      color: $nav-color;
      line-height: 24px;
      *:first-child :hover {
        color: $nav-color-hover;
      }
      .pmu-prev,
      .pmu-next {
        color: #999;
        font-family: $primary-font;
        font-size: 26px;
        &:hover {
          color: #000;
          background-color: transparent;
        }
      }
      .pmu-month {
        font-size: 24px;
        font-weight: 300;
        font-family: $primary-font;
      }
    }
    .pmu-years,
    .pmu-months {
      * {
        display: inline-block;
        line-height: 30px;
        width: 36px;
        margin: 4px;
        text-align: center;
      }
    }
    .pmu-day-of-week {
      color: $day-of-week;
      cursor: default;
    }
    .pmu-day-of-week,
    .pmu-days {
      .pmu-button {
        display: inline-block;
        width: 14.2857%;
        max-width: 30px;
        height: 30px;
        margin: 4px;
        line-height: 30px;
        text-align: center;
        border-radius: 15px;
      }
    }
    .pmu-day-of-week {
      > div {
        display: inline-block;
        width: 14.2857%;
        max-width: 30px;
        height: 30px;
        margin: 4px;
        line-height: 30px;
        text-align: center;
      }
    }
  }
  .pmu-instance:first-child .pmu-prev,
  .pmu-instance:last-child .pmu-next {
    display: block;
  }
  .pmu-instance:first-child,
  .pmu-instance:last-child {
    .pmu-month {
      &:hover {
        color: $color;
        background-color: transparent;
      }
    }
  }
  .pmu-instance:first-child:last-child .pmu-month {
    width: 12em;
  }
  &:not(.pmu-view-days) .pmu-days,
  &:not(.pmu-view-days) .pmu-day-of-week,
  &:not(.pmu-view-months) .pmu-months,
  &:not(.pmu-view-years) .pmu-years {
    display: none;
  }
}

