.subnav {
  display: none;
  padding: 16px 26px;
  background-color: #252525;
  @media (min-width: 1024px) {
    display: block;
  }
  &__link {
    display: block;
    padding: 15px 0;
    color: #999;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    &:hover {
      color: #fff;
    }
    span {
      margin-left: 12px;
      letter-spacing: 0.05em;
    }
  }
}
