/* Requirements */
[data-accordion] [data-content] {
  overflow: hidden;
  max-height: 0;
}

/* Basic Theme */
[data-accordion] {
  line-height: 1;
}

[data-control] {
  position: relative;
  padding: 23px 50px 23px 45px;
  color: #252525;
  background-color: #fff;
  font-family: $secondary-font;
  font-size: 14px;
  line-height: 24px;
  border-bottom: 1px solid #d6d7db;
  cursor: pointer;
  &:hover {
    background-color: #f0f0f0;
  }
  span:nth-child(1) {
    position: absolute;
    left: 20px;
    color: #999;
  }
}
[data-accordion].open > [data-control] {
  background-color: #f0f0f0;
}


[data-content] {
  background-color: #f0f0f0;
}
[data-accordion].open > [data-content] {
  border-bottom: 1px solid #d6d7db;
}
[data-content] > div {
  padding: 40px 30px 40px 45px;
  color: #252525;
  font-family: $secondary-font;
  font-size: 14px;
  line-height: 26px;
  overflow-x: auto;
}

[data-content] [data-accordion] {
  border: 0;
  padding: 0;
}

[data-accordion] > [data-control]::before,
[data-accordion] > [data-control]::after {
  content: "";
  position: absolute;
  right: 20px;
  top: 50%;
  width: 16px;
  height: 2px;
  background-color: #999;
}
[data-accordion] > [data-control]::after {
  transform: rotate(90deg);
}
[data-accordion].open > [data-control]::after {
  display: none;
}
[data-accordion] > [data-control]:hover::before,
[data-accordion] > [data-control]:hover::after {
  background-color: #0099ff;
}
