.icon {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  line-height: inherit;
  background-repeat: no-repeat;
  background-position: 50%;

  &--search {
    width: 16px;
    height: 16px;
    background-image: url(../assets/images/icons/icon-search.png);
  }
  &--load {
    width: 18px;
    height: 18px;
    background-image: url(../assets/images/icons/icon-load.png);
    background-size: 18px 16px;
  }
  &--mail {
    top: -2px;
    width: 16px;
    height: 13px;
    background-image: url(../assets/images/icons/icon-mail.png);
    background-size: 16px 13px;
  }
  &--tag {
    top: -1px;
    width: 17px;
    height: 16px;
    background-image: url(../assets/images/icons/icon-tag.png);
    background-size: 17px 16px;
  }


  &--fb {
    top: -1px;
    width: 30px;
    height: 30px;
    background: svg-load('assets/images/icons/icon-fb.svg');
    background-size: 30px;
    background-repeat: no-repeat;
    a:hover & {
      background-image: svg-inline(fb);
    }
  }
  &--vk {
    top: -1px;
    width: 30px;
    height: 30px;
    background: svg-load('assets/images/icons/icon-vk.svg');
    background-size: 30px;
    background-repeat: no-repeat;
    a:hover & {
      background-image: svg-inline(vk);
    }
  }
  &--tw {
    top: -1px;
    width: 30px;
    height: 30px;
    background: svg-load('assets/images/icons/icon-tw.svg');
    background-size: 30px;
    background-repeat: no-repeat;
    a:hover & {
      background-image: svg-inline(tw);
    }
  }
  &--views {
    top: -1px;
    width: 20px;
    height: 12px;
    background: svg-load('assets/images/icons/icon-views.svg');
    background-size: 20px 12px;
    background-repeat: no-repeat;
    .views--accent & {
      background-image: svg-inline(views);
    }
  }
  &--burger {
    top: -1px;
    width: 26px;
    height: 18px;
    background: svg-load('assets/images/icons/icon-burger.svg');
    background-size: 26px 18px;
    background-repeat: no-repeat;
  }
  &--nav-close {
    top: -1px;
    width: 26px;
    height: 22px;
    background-image: svg-load('assets/images/icons/icon-nav-close.svg');
    background-size: 26px 22px;
  }
  &--top-arrow {
    top: -1px;
    width: 20px;
    height: 20px;
    background-image: svg-load('assets/images/icons/icon-top-arrow.svg');
    background-size: 11px 13px;
  }


  &--analitycs {
    top: -2px;
    width: 18px;
    height: 18px;
    background-image: svg-load('assets/images/icons/icon-nav-analitycs.svg');
    background-size: 18px 15px;
    @media (min-width: 768px) and (max-width: 1023px) {
      width: 36px;
      height: 36px;
      background-size: 29px 22px;
    }
    .nav__link--active &,
    .nav__link:hover &,
    .active &,
    .subnav__link:hover & {
      background-image: svg-inline(analitycs);
    }
  }
  &--indicators {
    top: -2px;
    width: 18px;
    height: 18px;
    background-image: svg-load('assets/images/icons/icon-nav-indicators.svg');
