.footer {
  height: 160px;
  padding: 28px 0;
  background-color: #191919;
  @media (min-width: 1024px) {
    height: 140px;
  }
  &__top {
    display: none;
    margin-bottom: 26px;
  }
  &__top,
  &__bottom {
    @media (min-width: 1024px) {
      display: flex;
      justify-content: space-between;
    }
  }
  &__copyright {
    margin-bottom: 20px;
    color: #666;
    font-family: $roboto;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    @media (min-width: 1024px) {
      margin-bottom: 0;
      text-align: left;
    }
    > a {
      display: block;
      color: #666;
      &:hover,
      &:visited {
        color: #666;
      }
    }
  }
  &__social {
    text-align: center;
    @media (min-width: 1024px) {
      text-align: left;
    }
    a {
      display: inline-block;
      margin: 0 4px;
    }
  }
}


.footer-nav {
  display: flex;
  &__item {
    &:not(:last-child) {
      margin-right: 34px;
    }
  }
  &__link {
    display: block;
    color: #999;
    font-family: $secondary-font;
    font-size: 14px;
    &:hover {
      color: #ccc;
    }
  }
}
