.likely,
.likely__widget {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  margin: 0;
  padding: 0;
  text-indent: 0 !important;
  list-style: none !important;
  font-weight: normal;
  font-family: Helvetica Neue, Arial, sans-serif;
  font-size: inherit;
}
.likely {
  opacity: 0;
  font-size: 0 !important;
}
.likely_visible {
  opacity: 1;
  transition: opacity 0.1s ease-in;
}
.likely > * {
  display: inline-block;
  visibility: hidden;
}
.likely_visible > * {
  visibility: inherit;
}
.likely__widget {
  display: inline-block;
  position: relative;
  white-space: nowrap;
}
.likely__button,
.likely__counter {
  text-decoration: none;
  text-rendering: optimizeLegibility;
  display: inline-block;
  margin: 0;
  outline: 0;
}
.likely__button {
  position: relative;
  cursor: pointer;
  user-select: none;
}
.likely__counter {
  display: none;
  position: relative;
  font-weight: normal;
}
.likely_ready .likely__counter {
  display: inline-block;
}
.likely_ready .likely__counter_empty {
  display: none;
}
.likely__widget,
.likely__icon,
.likely__button,
.likely__counter {
  vertical-align: top;
}
.likely__widget {
  transition: background 0.33s ease-out, color 0.33s ease-out, fill 0.33s ease-out;
}
.likely__widget:hover,
.likely__widget:active,
.likely__widget:focus {
  transition: none;
  cursor: pointer;
}
@media (hover: none) {
  .likely__widget:hover,
  .likely__widget:active,
  .likely__widget:focus {
    transition: background 0.33s ease-out, color 0.33s ease-out, fill 0.33s ease-out;
    cursor: unset;
  }
}
.likely__icon {
  position: relative;
  text-align: left;
  display: inline-block;
}
.likely svg {
  position: absolute;
}
.likely__button,
.likely__counter {
  line-height: inherit;
  cursor: inherit;
}
.likely__button:empty {
  display: none;
}
.likely__counter {
  text-align: center;
}
/* Style options */
.likely .likely__widget {
  color: #fff;
}
.likely .likely__counter {
  background-size: 1px 1px;
  background-repeat: repeat-y;
  background-image: linear-gradient(to right, rgba(0,0,0,0.2) 0, rgba(0,0,0,0) 0.5px, rgba(0,0,0,0) 100%);
}


