/* Size options */
.likely {
  margin: 0 -6px -12px;
}
.likely .likely__widget {
  height: 33px;
  margin: 0 6px 12px;
  padding: 0 20px 0 0;
  font-family: $primary-font;
  font-size: 11px;
  font-weight: 700;
  line-height: 30px;
  border-radius: 3px;
  text-transform: uppercase;
}
.likely .likely__icon {
  width: 32px;
  height: 32px;
}
.likely .likely__button {
  padding: 2px 6px 2px 0;
}
.likely .likely__counter {
  padding: 2px 6px;
}
.likely svg {
  left: 7px;
  top: 7px;
  width: 18px;
  height: 18px;
}
