/* Common Styles
----------------------------------------------------------------------------- */
html {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  height: 100%;
  font-family: $primary-font;
  background-color: #d6d7db;
}

*, *:focus {
  outline: none;
}
*::-moz-focus-inner {
  border: 0 none;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

ul {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
  }
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  transition: all $mid;
}

textarea {
  resize: vertical;
}

hr {
  display: block;
  height: 1px;
  border: 0 none;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  @include clearfix;
  @media (min-width: 768px) {
    padding: 0 30px;
  }
}

.content {
  padding: 20px 0;
  @media (min-width: 768px) {
    padding: 30px 0;
  }
  @media (min-width: 1024px) {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    @media (min-width: 1024px) {
      padding-right: 30px;
    }
  }
  &__right {
    @media (min-width: 1024px) {
      width: 280px;
      min-width: 280px;
    }
  }
}

.h1, .h2, .h3 {
  color: #191919;
  font-family: $primary-font;
}
.h1 {
  margin-top: 11px;
  margin-bottom: 11px;
  font-size: 36px;
  font-weight: 300;
  line-height: 40px;
  letter-spacing: -0.025em;
  @media (min-width: 768px) {
    margin-top: 15px;
    margin-bottom: 19px;
    font-size: 56px;
    line-height: 62px;
  }
}
.h2 {
  margin-top: 46px;
  font-size: 30px;
  font-weight: 400;
  letter-spacing: -0.01em;
}
.h3 {
  margin: 0 0 16px;
  font-size: 20px;
  font-weight: 400;
  @media (min-width: 768px) {
    font-size: 24px;
  }
}

.subheader {
  background-color: #fff;
}

.views {
  color: #999;
  font-family: $secondary-font;
  font-size: 12px;
  span {
    margin-left: 10px;
  }
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(#191919, 0.6);
  z-index: 99;
}

.accordion + .btn {
  margin-top: 30px;
}

.desc {
  padding: 35px 50px 75px 30px;
  background-color: #fff;
  p {
    font-family: $secondary-font;
    font-size: 14px;
    line-height: 26px;
  }
}

.platform-graph {
  width: 100%;
  min-width: 540px;
  height: auto;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__logo {
    display: none;
    @media (min-width: 768px) {
      display: block;
    }
  }
}

.to-top {
  display: none;
  width: 50px;
  height: 50px;
  background-color: rgba(#282828, 0.5);
  float: right;
  &:hover {
    background-color: rgba(#282828, 0.7);
  }
  @media (min-width: 768px) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @media (min-width: 1024px) {
    margin-top: -50px;
  }
}

.browserupgrade {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}
