.post {
  background-color: #fff;
  &__img {
    img {
      width: 100%;
      height: auto;
    }
    .img__min {
      display: block;
      @media (min-width: 490px) {
        display: none;
      }
    }
    .img__max {
      display: none;
      @media (min-width: 490px) {
        display: block;
      }
    }
  }
  &__head {
    display: flex;
    align-items: center;
    height: 60px;
    padding: 0 30px;
    border-bottom: 1px solid #f0f0f0;
  }
  &__date {
    color: #999;
    font-family: $secondary-font;
    font-size: 12px;
    font-weight: 300;
    @media (min-width: 768px) {
      font-size: 14px;
    }
  }
  &__views {
    margin-left: auto;
  }
  &__body {
    padding: 37px 25px 40px;
    @media (min-width: 768px) {
      padding: 37px 55px 55px 30px;
    }
    > p {
      color: #252525;
      font-family: $secondary-font;
      font-size: 14px;
      font-weight: 300;
      line-height: 24px;
      @media (min-width: 768px) {
        line-height: 26px;
      }
    }
  }
  &__note {
    padding: 30px 0;
    color: #999;
    font-family: $secondary-font;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
  }
  &__footer {
    min-height: 70px;
    padding: 30px 30px 20px;
    background-color: #f0f0f0;
  }
}
