.post-list {
  display: flex;
  flex-wrap: wrap;
  &__item {
    width: 100%;
    margin-bottom: 20px;
    @media (min-width: 768px) {
      margin-bottom: 30px;
    }
    @media (min-width: 1200px) {
      width: calc(50% - 15px);
    }
    &:nth-child(odd) {
      @media (min-width: 1200px) {
        margin-right: 30px;
      }
    }
    &--col2 {
      width: 50%;
    }
  }
  &__img {
    display: block;
    img {
      width: 100%;
      height: auto;
    }
    .img__min {
      display: block;
      @media (min-width: 440px) {
        display: none;
      }
      @media (min-width: 1200px) {
        display: block;
      }
    }
    .img__max {
      display: none;
      @media (min-width: 440px) {
        display: block;
      }
      @media (min-width: 1200px) {
        display: none;
      }
    }
  }
  &__body {
    padding: 24px;
    background-color: #fff;
  }
  &__date {
    color: #0099ff;
    font-family: $secondary-font;
    font-size: 12px;
  }
  &__title {
    display: inline-block;
    margin-top: 8px;
    color: #191919;
    font-size: 20px;
    font-weight: 300;
    &:hover {
      color: #0099ff;
    }
    @media (min-width: 768px) {
      font-size: 22px;
    }
  }
  &__footer {
    padding-top: 40px;
    @media (min-width: 768px) {
      display: flex;
      justify-content: space-between;
    }
  }
  &__tag {
    display: inline-block;
    margin-right: 9px;
    margin-bottom: 14px;
    color: #999;
    font-family: $secondary-font;
    font-size: 12px;
    &:hover {
      color: #0099ff;
    }
    @media (min-width: 768px) {
      margin-bottom: 0;
    }
  }
}
