.question {
  margin-top: 30px;
  &__link {
    display: block;
    width: 100%;
    height: 70px;
    padding: 0 24px;
    color: #fff;
    background-color: #0099ff;
    background-image: url(../assets/images/icons/icon-question.png);
    background-repeat: no-repeat;
    background-position: 90% 6px;
    font-size: 22px;
    font-weight: 300;
    line-height: 70px;
    letter-spacing: -0.01em;
    @media (min-width: 768px) and (max-width: 1023px) {
      text-align: center;
      background-position: 98% 6px;
    }
    &:hover {
      background-color: rgba(#0099ff, 0.75);
    }
  }
}
