.best-list {
  touch-action: manipulation;
  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
  }
  &__item {
    width: 100%;
    padding: 20px;
    margin-bottom: 30px;
    background-color: #fff;
    @media (min-width: 768px) {
      width: calc(50% - 30px);
      margin: 0 15px 30px;
    }
    @media (min-width: 1200px) {
      width: calc(33.3333% - 30px);
    }
  }
  &__body {
    display: flex;
  }
  &__date {
    font-weight: 700;
    span {
      color: #666;
      font-size: 36px;
      line-height: 34px;
    }
    div {
      color: #999;
      font-size: 13px;
    }
  }
  &__title {
    padding-left: 22px;
    color: #252525;
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    letter-spacing: -0.01em;
    @media (min-width: 768px) {
      font-size: 22px;
      line-height: 28px;
    }
    &:hover {
      color: #0192f3;
    }
  }
  &__footer {
    padding-top: 20px;
  }
}
