.select2-selection--multiple {
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: text;

  .select2-selection__rendered {
    list-style: none;
    margin: 0;
    padding: 0 5px;
    width: 100%;

    li {
      list-style: none;
    }
  }

  .select2-selection__placeholder {
    color: #999;
    margin-top: 5px;
    float: left;
  }

  .select2-selection__clear {
    cursor: pointer;
    float: right;
    font-weight: bold;
    margin-top: 5px;
    margin-right: 10px;
  }

  .select2-selection__choice {
    height: 36px;
    border-radius: 3px;
    background-color: #d6d7db;
    cursor: default;
    float: left;
    margin-right: 4px;
    margin-top: 5px;
    padding-left: 16px;
    padding-right: 15px;
    line-height: 37px;
    color: #666;
    font-family: $secondary-font;
    font-size: 14px;
  }

  .select2-selection__choice__remove {
    position: relative;
    top: 1px;
    float: right;
    margin-left: 47px;
    color: #666;
    cursor: pointer;
    font-weight: bold;
    &:hover {
      color: #333;
    }
  }
}

&.select2-container--focus {
  .select2-selection--multiple {
    outline: 0;
  }
}

&.select2-container--disabled {
  .select2-selection--multiple {
    background-color: #eee;
    cursor: default;
  }

  .select2-selection__choice__remove {
    display: none;
  }
}
