.header {
  height: 60px;
  background-color: #191919;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    width: 161px;
    height: 60px;
    background-image: url(../assets/images/logo.png);
    background-repeat: no-repeat;
    background-position: 50%;
  }
  &__social {
    display: none;
    @media (min-width: 1024px) {
      display: block;
    }
    a {
      display: inline-block;
      width: 30px;
      height: 30px;
      &:not(:last-child) {
        margin-right: 8px;
      }
    }
  }
  &__burger {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    padding: 0 10px;
    @media (min-width: 1024px) {
      display: none;
    }
  }
}
