.tags {
  position: relative;
  padding-left: 70px;
  &__title {
    position: absolute;
    left: 0;
    > span {
      margin-left: 7px;
      color: #191919;
      font-size: 12px;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    > li {
      margin-right: 12px;
      margin-bottom: 4px;
      @media (min-width: 768px) {
        margin-bottom: 10px;
      }
    }
  }
  &__link {
    color: #999;
    font-family: $secondary-font;
    font-size: 12px;
    font-weight: 300;
    @media (min-width: 768px) {
      font-size: 14px;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}
