.search {
  position: relative;
  height: 46px;
  opacity: 0;
  &__btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 70px;
    height: 48px;
    background-color: #0099ff;
    border: 0 none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    transition: all $mid;
    &:hover {
      background-color: rgba(#0099ff, 0.7);
    }
  }
}
