@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: local("Roboto Light"), local("Roboto-Light"), url("../assets/fonts/roboto-v16-latin_cyrillic-300.woff2") format("woff2"), url("../assets/fonts/roboto-v16-latin_cyrillic-300.woff") format("woff");
}

@font-face {
  font-family: "Pragmatica";
  font-style: normal;
  font-weight: 400;
  src: local("Pragmatica Regular"), local("Pragmatica-Regular"), url("../assets/fonts/pragmatica-book-reg.woff2") format("woff2"), url("../assets/fonts/pragmatica-book-reg.woff") format("woff");
}

@font-face {
  font-family: "Circe";
  font-weight: 300;
  src: url("../assets/fonts/circe-light.otf") format("opentype");
}

@font-face {
  font-family: "Circe";
  font-weight: 400;
  src: url("../assets/fonts/circe-regular.otf") format("opentype");
}

@font-face {
  font-family: "Circe";
  font-weight: 700;
  src: url("../assets/fonts/circe-bold.otf") format("opentype");
}

/* Common Styles
----------------------------------------------------------------------------- */
html {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  height: 100%;
  font-family: "Circe", "Roboto", "Source Sans Pro", "Open Sans", Arial, sans-serif;
  background-color: #d6d7db;
}

*, *:focus {
  outline: none;
}

*::-moz-focus-inner {
  border: 0 none;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  transition: all 350ms;
}

textarea {
  resize: vertical;
}

hr {
  display: block;
  height: 1px;
  border: 0 none;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.container:before, .container:after {
  content: "";
  display: table;
}

.container:after {
  clear: both;
}

@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}

.content {
  padding: 20px 0;
}

@media (min-width: 768px) {
  .content {
    padding: 30px 0;
  }
}

@media (min-width: 1024px) {
  .content {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 1024px) {
  .content__left {
    padding-right: 30px;
  }
}

@media (min-width: 1024px) {
  .content__right {
    width: 280px;
    min-width: 280px;
  }
}

.h1, .h2, .h3 {
  color: #191919;
  font-family: "Circe", "Roboto", "Source Sans Pro", "Open Sans", Arial, sans-serif;
}

.h1 {
  margin-top: 11px;
  margin-bottom: 11px;
  font-size: 36px;
  font-weight: 300;
  line-height: 40px;
  letter-spacing: -0.025em;
}

