  font-size: 12px;
}

.views span {
  margin-left: 10px;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(25, 25, 25, 0.6);
  z-index: 99;
}

.accordion + .btn {
  margin-top: 30px;
}

.desc {
  padding: 35px 50px 75px 30px;
  background-color: #fff;
}

.desc p {
