}

.title__logo {
  display: none;
}

@media (min-width: 768px) {
  .title__logo {
    display: block;
  }
}

.to-top {
  display: none;
  width: 50px;
  height: 50px;
  background-color: rgba(40, 40, 40, 0.5);
