.popular {
  margin-top: 30px;
  padding: 25px;
  background-color: #fff;
  &__items {
    @media (min-width: 768px) and (max-width: 1023px) {
      display: flex;
      flex-wrap: wrap;
    }
  }
  &__item {
    margin-bottom: 20px;
    @media (min-width: 768px) and (max-width: 1023px) {
      &:not(:nth-child(3n)) {
        margin-right: 30px;
      }
      width: calc(33.3333% - 20px);
    }
  }
  &__title {
    margin-bottom: 19px;
    color: #191919;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    span {
      margin-left: 15px;
      letter-spacing: 0.05em;
    }
  }
  &__link {
    display: block;
    margin-bottom: 8px;
    color: #191919;
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    &:hover {
      color: #0192f3;
    }
  }
}
