@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: local("Roboto Light"), local("Roboto-Light"),
    url("../assets/fonts/roboto-v16-latin_cyrillic-300.woff2") format("woff2"),
    url("../assets/fonts/roboto-v16-latin_cyrillic-300.woff") format("woff");
}

@font-face {
  font-family: "Pragmatica";
  font-style: normal;
  font-weight: 400;
  src: local("Pragmatica Regular"), local("Pragmatica-Regular"),
    url("../assets/fonts/pragmatica-book-reg.woff2") format("woff2"),
    url("../assets/fonts/pragmatica-book-reg.woff") format("woff");
}

@font-face {
  font-family: "Circe";
  font-weight: 300;
  src: url("../assets/fonts/circe-light.otf") format("opentype");
}

@font-face {
  font-family: "Circe";
  font-weight: 400;
  src: url("../assets/fonts/circe-regular.otf") format("opentype");
}

@font-face {
  font-family: "Circe";
  font-weight: 700;
  src: url("../assets/fonts/circe-bold.otf") format("opentype");
}
