.nav {
  display: none;
  @media (min-width: 1024px) {
    display: flex;
  }
  &__item {
    &:not(:last-child) {
      margin-right: 35px;
    }
  }
  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    height: 60px;
    color: #999;
    font-size: 12px;
    text-transform: uppercase;
    &:hover {
      color: #fff;
    }
    &--active {
      color: #fff;
      &:after {
        position: absolute;
        content: "";
        left: 50%;
        bottom: 0;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 4px 4px 4px;
        border-color: transparent transparent #edeef2 transparent;
      }
    }
    span {
      margin-left: 10px;
      letter-spacing: 0.05em;
    }
  }
}
