.partner-list {
  &__item {
    display: block;
    margin-bottom: 20px;
    background-color: #fff;
    @media (min-width: 768px) {
      display: flex;
      margin-bottom: 30px;
    }
    &:hover .partner-list__title {
      color: #0099ff;
    }
  }
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    @media (max-width: 767px) {
      min-height: 130px;
      border-bottom: 1px solid #f0f0f0;
    }
    @media (min-width: 768px) {
      width: 37%;
      border-right: 1px solid #f0f0f0;
    }
  }
  &__body {
    padding: 20px 25px;
    color: #252525;
    @media (min-width: 768px) {
      width: 63%;
      padding: 26px 60px 26px 30px;
    }
  }
  &__title {
    margin-bottom: 14px;
    font-size: 24px;
    font-weight: 400;
    transition: color $mid;
  }
  &__txt {
    font-family: $secondary-font;
    font-size: 14px;
    line-height: 25px;
  }
}
