.mobile-nav {
    position: absolute;
    top: 0;
    right: -100%;
    width: 260px;
    padding: 75px 0 50px;
    background-color: #141414;
    z-index: 100;
    transition: right 350ms ease-in-out;
    @media (min-width: 768px) {
      width: 540px;
      padding: 140px 0 80px;
    }
    @media (min-width: 1024px) {
      display: none;
    }
    &__link {
      display: block;
      padding: 18px 20px 18px 50px;
      color: #999;
      font-size: 12px;
      font-weight: 400;
      text-transform: uppercase;
      @media (min-width: 768px) {
        padding: 30px 20px 30px 110px;
        font-size: 22px;
      }
      &:hover,
      &.active {
        color: #fff;
      }
      span {
        margin-left: 10px;
        @media (min-width: 768px) {
          margin-left: 20px;
        }
      }
    }
    &__social {
      margin-top: 30px;
      padding-left: 50px;
      @media (min-width: 768px) {
        padding-left: 110px;
      }
      a {
        display: inline-block;
        width: 30px;
        height: 30px;
        &:not(:last-child) {
          margin-right: 8px;
        }
      }
    }
    &__close {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      padding: 20px 30px;
    }
}
