.select2-selection--multiple {
  cursor: pointer;
  display: block;
  min-height: 46px;
  user-select: none;

  .select2-selection__rendered {
    display: inline-block;
    overflow: hidden;
    padding-left: 8px;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.select2-search--inline {
  float: left;

  .select2-search__field {
    border: none;
    font-size: 100%;
    margin-top: 5px;
    margin-left: 6px;
    padding: 0;

    &::-webkit-search-cancel-button {
      -webkit-appearance: none;
    }
  }
}
