.subscription {
  margin-top: 30px;
  padding: 25px;
  background-color: #0099ff;
  &__title {
    margin-bottom: 19px;
    color: #fff;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    span {
      margin-left: 15px;
      letter-spacing: 0.05em;
    }
  }
  &__field {
    @media (min-width: 768px) and (max-width: 1023px) {
      display: flex;
    }
  }
  &__input {
    width: 100%;
    height: 40px;
    margin-bottom: 10px;
    padding: 0 15px;
    background-color: #fff;
    font-family: $secondary-font;
    font-size: 12px;
    font-weight: 300;
    border: 0 none;
    border-radius: 4px;
    @media (min-width: 768px) and (max-width: 1023px) {
      margin-right: 11px;
    }
    @media (min-width: 768px) {
      font-size: 14px;
    }
    &::placeholder {
      color: #999;
      font-size: 12px;
      font-weight: 300;
      font-family: $secondary-font;
      @media (min-width: 768px) {
        font-size: 14px;
      }
    }
  }
  &__btn {
    margin-top: 10px;
    @media (min-width: 768px) and (max-width: 1023px) {
      margin: 0;
    }
  }
}
