.btn {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  white-space: nowrap;
  border: 0 none;
  border-radius: 4px;
  user-select: none;
  cursor: pointer;
  transition: all $mid;

  &--default {
    width: 100%;
    height: 40px;
    color: #fff;
    background-color: #0064a6;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 300;
    &:hover {
      background-color: rgba(#0064a6, 0.75);
    }
  }

  &--load {
    width: 100%;
    padding: 17px 0;
    color: #999;
    font-family: $roboto;
    font-size: 14px;
    font-weight: 300;
    background-color: #282828;
    &:hover {
      background-color: rgba(#282828, 0.75);
    }
    span {
      margin-left: 13px;
    }
  }
}
