.filter {
  position: relative;
  height: 51px;
  margin-top: 2px;
  @media (max-width: 767px) {
    margin-right: -20px;
  }
  @media (min-width: 1024px) {
    display: flex;
    justify-content: space-between;
  }
  &::after {
    position: absolute;
    content: "";
    top: 2px;
    right: 0;
    width: 34px;
    height: 49px;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), #fff);
    @media (min-width: 768px) {
      display: none;
    }
  }
  &__type {
    display: flex;
    align-items: center;
    @media (max-width: 767px) {
      overflow-x: auto;
    }
  }
  &__type-item {
    &:not(:last-child) {
      margin-right: 25px;
    }
  }
  &__type-link {
    display: block;
    padding-top: 18px;
    padding-bottom: 15px;
    color: #777;
    font-family: $secondary-font;
    font-size: 14px;
    border-bottom: 2px solid transparent;
    &:hover {
      color: #191919;
    }
    &--active {
      color: #252525;
      border-bottom: 2px solid #0099ff;
    }
  }
  &__calendar {
    display: none;
    @media (min-width: 1024px) {
      display: block;
    }
  }
  &__year {
    display: none;
    @media (min-width: 1024px) {
      display: flex;
      align-items: center;
    }
  }
}


.calendar {
  width: 185px;
  padding-top: 18px;
  padding-bottom: 15px;
  color: #777;
  font-family: $secondary-font;
  font-size: 14px;
  text-align: right;
  border: 0 none;
  background-image: url(../assets/images/icons/icon-calendar.png);
  background-size: 14px 16px;
  background-repeat: no-repeat;
  background-position: left 50%;
  cursor: pointer;
  &:hover {
    background-image: url(../assets/images/icons/icon-calendar-hover.png);
  }
}



